import { apiInstance } from "./index.js";

const api = apiInstance();

async function register(user, success, fail) {
  await api
    .post(`/user/register`, JSON.stringify(user))
    .then(success)
    .catch(fail);
}

async function checkId(userid, success, fail) {
  await api.get(`/user/check/${userid}`).then(success).catch(fail);
}
async function getById(userid, success, fail) {
  await api.get(`/user/get/${userid}`).then(success).catch(fail);
}

async function login(user, success, fail) {
  await api.post(`/user/login`, JSON.stringify(user)).then(success).catch(fail);
}

async function findById(userid, success, fail) {
  api.defaults.headers["access-token"] = sessionStorage.getItem("access-token");
  await api.get(`/user/info/${userid}`).then(success).catch(fail);
}

async function modify(user, success, fail) {
  api
    .put(`/user/modify/${user.userid}`, JSON.stringify(user))
    .then(success)
    .catch(fail);
}

async function deleteMember(userid, success, fail) {
  api
    .delete(`/user/delete/${userid}`, JSON.stringify(userid))
    .then(success)
    .catch(fail);
}

// function logout(success, fail)

export { register, checkId, getById, login, findById, modify, deleteMember };
