import {
  sidoList,
  gugunList,
  dongList,
  houseList,
  houseListByText,
  storeList,
} from "@/api/house.js";

const houseStore = {
  namespaced: true,
  state: {
    sidos: [{ value: null, text: "선택하세요(시,도)" }],
    guguns: [{ value: null, text: "선택하세요(구,군)" }],
    dongs: [{ value: null, text: "선택하세요(동)" }],
    houses: [],
    house: null,
    currentPage: 1,
    perPage: 5,
    stores: [],
  },

  getters: {},

  mutations: {
    SET_SIDO_LIST: (state, sidos) => {
      sidos.forEach((sido) => {
        state.sidos.push({ value: sido.sidoCode, text: sido.sidoName });
      });
    },
    SET_GUGUN_LIST: (state, guguns) => {
      guguns.forEach((gugun) => {
        state.guguns.push({ value: gugun.gugunCode, text: gugun.gugunName });
      });
    },
    SET_DONG_LIST: (state, dongs) => {
      dongs.forEach((dong) => {
        state.dongs.push({ value: dong.dongCode, text: dong.dongName });
      });
    },
    CLEAR_SIDO_LIST: (state) => {
      state.sidos = [{ value: null, text: "선택하세요(시,도)" }];
    },
    CLEAR_GUGUN_LIST: (state) => {
      state.guguns = [{ value: null, text: "선택하세요(구,군)" }];
    },
    CLEAR_DONG_LIST: (state) => {
      state.dongs = [{ value: null, text: "선택하세요(동)" }];
    },
    SET_HOUSE_LIST: (state, houses) => {
      //   console.log(houses);
      state.houses = houses;
    },
    SET_DETAIL_HOUSE: (state, house) => {
      state.house = house;
    },
    SET_CURRENT_PAGE: (state, currentPage) => {
      state.currentPage = currentPage;
    },
    SET_PER_PAGE: (state, perPage) => {
      state.perPage = perPage;
    },
    SET_STORE_LIST: (state, stores) => {
      state.stores = stores;
    },
  },

  actions: {
    getSido: ({ commit }) => {
      sidoList(
        ({ data }) => {
          // console.log(data);
          commit("SET_SIDO_LIST", data);
        },
        (error) => {
          console.log(error);
        },
      );
    },
    getGugun: ({ commit }, sidoCode) => {
      const params = {
        sido: sidoCode,
      };
      gugunList(
        params,
        ({ data }) => {
          // console.log(commit, response);
          commit("SET_GUGUN_LIST", data);
        },
        (error) => {
          console.log(error);
        },
      );
    },
    getDong: ({ commit }, gugunCode) => {
      const params = {
        gugun: gugunCode,
      };
      dongList(
        params,
        ({ data }) => {
          // console.log(commit, response);
          commit("SET_DONG_LIST", data);
        },
        (error) => {
          console.log(error);
        },
      );
    },
    getHouseList: ({ commit }, dongCode) => {
      // vue cli enviroment variables 검색
      //.env.local file 생성.
      // 반드시 VUE_APP으로 시작해야 한다.
      // const SERVICE_KEY = process.env.VUE_APP_APT_DEAL_API_KEY;
      // //   const SERVICE_KEY =
      // //     "9Xo0vlglWcOBGUDxH8PPbuKnlBwbWU6aO7%2Bk3FV4baF9GXok1yxIEF%2BIwr2%2B%2F%2F4oVLT8bekKU%2Bk9ztkJO0wsBw%3D%3D";
      // const params = {
      //   LAWD_CD: gugunCode,
      //   DEAL_YMD: "202110",
      //   serviceKey: decodeURIComponent(SERVICE_KEY),
      // };
      const params = {
        dong: dongCode,
      };
      houseList(
        params,
        ({ data }) => {
          //   console.log(response.data.response.body.items.item);
          commit("SET_HOUSE_LIST", data);
        },
        (error) => {
          console.log(error);
        },
      );
    },

    getHouseListByText: ({ commit }, text) => {
      const params = {
        text: text,
      };
      houseListByText(
        params,
        ({ data }) => {
          //   console.log(response.data.response.body.items.item);
          commit("SET_HOUSE_LIST", data);
        },
        (error) => {
          console.log(error);
        },
      );
    },

    detailHouse: ({ commit }, house) => {
      // 나중에 house.일련번호를 이용하여 API 호출
      commit("SET_DETAIL_HOUSE", house);
    },

    getCurrentPage: ({ commit }, currentPage) => {
      commit("SET_CURRENT_PAGE", currentPage);
    },

    getPerPage: ({ commit }, perPage) => {
      commit("SET_CURRENT_PAGE", 1);
      commit("SET_PER_PAGE", perPage);
    },

    getStoreList: ({ commit }, payload) => {
      //alert(payload.type);
      const params = {
        dong: payload.dongCode,
        type: payload.type,
      };
      storeList(
        params,
        ({ data }) => {
          //console.log(data.response.body.items.item);
          commit("SET_STORE_LIST", data);
        },
        (error) => {
          console.log(error);
        },
      );
    },
  },
};

export default houseStore;
