import Vue from "vue";
import VueRouter from "vue-router";
import HomeView from "../views/HomeView.vue";

import store from "@/store/index.js";

Vue.use(VueRouter);

// https://router.vuejs.org/kr/guide/advanced/navigation-guards.html
const onlyAuthUser = async (to, from, next) => {
  // console.log(store);
  const checkUserInfo = store.getters["memberStore/checkUserInfo"];
  const getUserInfo = store._actions["memberStore/getUserInfo"];
  let token = sessionStorage.getItem("access-token");
  if (checkUserInfo == null && token) {
    await getUserInfo(token);
  }
  if (checkUserInfo === null) {
    alert("로그인이 필요한 페이지입니다..");
    next({ name: "signIn" });
    // router.push({ name: "signIn" });
  } else {
    // console.log("로그인 했다.");
    next();
  }
};

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: "/user",
    name: "user",
    component: () => import("@/views/MemberView.vue"),
    children: [
      {
        path: "singin",
        name: "signIn",
        component: () => import("@/components/user/MemberLogin.vue"),
      },
      {
        path: "singup",
        name: "signUp",
        component: () => import("@/components/user/MemberRegister.vue"),
      },
      {
        path: "mypage",
        name: "mypage",
        beforeEnter: onlyAuthUser,
        component: () => import("@/components/user/MemberMyPage.vue"),
      },
      {
        path: "modify",
        name: "modify",
        beforeEnter: onlyAuthUser,
        component: () => import("@/components/user/MemberModify.vue"),
      },
      {
        path: "delete",
        name: "delete",
        beforeEnter: onlyAuthUser,
        component: () => import("@/components/user/MemberDelete.vue"),
      },
    ],
  },
  {
    path: "/board",
    name: "board",
    component: () => import("@/views/BoardView.vue"),
    redirect: "/board/list",
    children: [
      {
        path: "list",
        name: "boardList",
        component: () => import("@/components/board/BoardList.vue"),
      },
      {
        path: "write",
        name: "boardRegister",
        beforeEnter: onlyAuthUser,
        component: () => import("@/components/board/BoardRegister.vue"),
      },
      {
        path: "detail/:articleno",
        name: "boardDetail",
        beforeEnter: onlyAuthUser,
        component: () => import("@/components/board/BoardDetail.vue"),
      },
      {
        path: "modify/:articleno",
        name: "boardModify",
        beforeEnter: onlyAuthUser,
        component: () => import("@/components/board/BoardModify.vue"),
      },
    ],
  },
  {
    path: "/instagram",
    name: "instagram",
    component: () => import("@/views/InstagramView.vue"),
  },
  {
    path: "/house",
    name: "house",
    beforeEnter: onlyAuthUser,
    component: () => import("@/views/HouseView.vue"),
  },
  {
    path: "/todo",
    name: "todo",
    beforeEnter: onlyAuthUser,
    component: () => import("@/views/TodoView.vue"),
  },
  {
    path: "/QnA",
    name: "QnA",
    component: () => import("@/views/QnAView.vue"),
    redirect: "/QnA/list",
    children: [
      {
        path: "list",
        name: "questionList",
        component: () => import("@/components/QnA/question/QuestionList.vue"),
      },
      {
        path: "write",
        name: "questionRegister",
        beforeEnter: onlyAuthUser,
        component: () =>
          import("@/components/QnA/question/QuestionRegister.vue"),
      },
      {
        path: "detail/:articleno",
        name: "questionDetail",
        beforeEnter: onlyAuthUser,
        component: () => import("@/components/QnA/question/QuestionDetail.vue"),
      },
      {
        path: "modify/:articleno",
        name: "questionModify",
        beforeEnter: onlyAuthUser,
        component: () => import("@/components/QnA/question/QuestionModify.vue"),
      },
      {
        path: "delete/:articleno",
        name: "questionDelete",
        component: () => import("@/components/QnA/question/QuestionDelete.vue"),
      },
      {
        path: "answer/write/:questionno",
        name: "answerRegister",
        component: () => import("@/components/QnA/answer/AnswerRegister.vue"),
      },
      {
        path: "answer/delete/:questionno/:answerno",
        name: "answerDelete",
        component: () => import("@/components/QnA/answer/AnswerDelete.vue"),
      },
      {
        path: "answer/modify/:questionno/:answerno",
        name: "answerModify",
        component: () => import("@/components/QnA/answer/AnswerModify.vue"),
      },
    ],
  },
  {
    path: "/ad",
    name: "ad",
    component: () => import("@/views/AdView.vue"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
